<template>
		
	<div class="wrapper">
		<div class="head"></div>
		<h4 class="wel">注册</h4>
		<span class="wel-yw">register</span>
		<div class="login-box">
			<input class="user" v-model="us" type="text" placeholder="账号（账号须小于10位数）">
			<input class="pas" v-model="pas" type="password" placeholder="密码">
		</div>
		<div class="jzPas">
			<input class="pasK" v-model="val" type="radio" name="ren" id="man" value="男">
			<label for="man">男</label>
			<input class="pasK1" v-model="val" type="radio" name="ren" id="feil" value="女">
			<label for="feil">女</label>
		</div>
		<input class="login" type="submit" @click.stop.prevent= "reg" value="注册">
		<!-- <span class="name">吕星辰</span> -->
	</div>
	<!-- </div> -->
</template>

<script>
export default {
	name: 'register',
	data () {
		return {
			us:"",
			pas: "" ,
			val:""
		}
	},
	methods:{
		reg(){
			let info = {
				us : this.us,
				pas : this.pas,
				val : this.val
			}
			if(this.us.length && this.pas.length && this.val.length){
				this.$axios.get("http://localhost:12306/register",{
					params:info
					}).then((res)=>{
					console.log(res)
					if(res.data == "ok"){
						alert("注册成功，亲，请记住您的账号和密码！")
						this.$router.push("/")
						
					}else if(res.data == "fail"){
						alert("账号重复啦")
						this.us = "";
						this.pas = "";
					}
				}).catch((e)=>{
					console.log(e)
				})
			}else if(this.us.length === 0){
				alert("请注册账号")
			}else if(this.pas.length === 0){
				alert("请注册密码")
			}else if(this.val.length === 0){
				alert("请填写性别")
			}
		}
	},
}
</script>
<style scoped lang="stylus">
.wrapper
	position absolute
	top 370px
	left 50%
	transform translate(-50%,-50%)
	display flex 
	flex-direction column
	align-items center
	box-sizing border-box
	padding 15px
	width 334px;
	height 380px;
	background #fff
	opacity 0.9
	border-radius:5px
	.head
		width 80px
		height 80px
		border-radius 50%
		background: url("https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1551978983773&di=60fd982b279d786957b362faf168daff&imgtype=0&src=http%3A%2F%2Fpic32.nipic.com%2F20130816%2F13455593_183010394000_2.jpg")
		background-size:100% 100%
		margin-top:-50px
	.wel
		color:#399aec
		margin-top:15px
	.wel-yw
		margin-top:-20px
		color:#399aec
		font-size:11px
	.login-box
		width:100%
		display flex
		flex-direction : column
		margin-top:18px
		.user
			height:26px
			padding:8px 12px
			font-size:15px
			border-radius:6px
			border:1px solid #bbb
			outline none
		.pas
			margin:15px 0
			height:26px
			padding:8px 12px
			font-size:15px
			border-radius:6px
			border:1px solid #bbb
			outline none
	.jzPas
		// margin-left:-200px
		display flex
		justify-content center
		align-items center
		color:#666
		font-size:13px
		.pasK
			margin-right:8px
			width:16px
			height:16px
		.pasK1
			margin-left:30px
			width:16px
			height:16px
	.login
		width:100%
		height:40px
		font-size:16px
		margin-top:20px
		background #399aec
		border:none
		border-radius:6px
		color:#fff
	.name
		margin-top:16px
		color:#a6a6a6
		font-size:15px
		margin-right:-140px
		cursor: pointer
		&:hover
			color:#399aec
</style>



