<template>
	<div class="hello">
		<router-link to="/" tag="div" class="return">返回</router-link>
		<div class="robot-box">
			<div class="title">聊天机器人</div>
			<div class="better-wrapper" ref="wrapper">
				<div class="better-content" ref="content">
					<div class="bgImg"></div>
					<div class="box" v-for="(item,index) in message" :key="index">
						<!-- 判断是自己还是机器人 -->
						<div :class='item.fx?"right":"left"'>
							<div class="r-head"></div>
							<div class="l-head"></div>
							<!-- <div class="l-head"></div> -->
							<div :class="item.self?'r-cont':'l-cont'">{{item.content?item.selfContent:item.robotContent}}</div>
						</div>
					</div>
				</div>
			</div>
			<div class="bottom-box">
				<div class="content">
					<input class="inp" type="text" v-model="inputContent" placeholder="请输入对话内容 ···">
					<button class="se" @click="send">发送</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>

export default {
	name: 'mainPage',
	data(){
		return{
			inputContent:"",
			message:[]
		}
	},
	methods:{
		send(){//这块定义的是自己，push到数组自己的信息，把自己渲染到页面
			if( this.inputContent.length == 0 ){
				alert("请输入内容")
			}else{
				this.message.push({
					selfContent:this.inputContent,
					self : true,
					content: true,
					fx:true
				})
				this.$axios.get("http://localhost:12306/reqRobot",{
					params:{
						selfContent:this.inputContent
					}
				}).then((res)=>{
					this.message.push({
						robotContent:res.data.text,
						self : false,
						content: false,
						fx:false
					})
					this.inputContent = ""
				})
			}
		},
		// robotSend(){
		// 	this.message.push({
		// 		robotContent:"机器机器人人机器人人机器人人机器人人机器人人机器人人人人",
		// 		self : false,
		// 		content: false,
		// 		fx:false
		// 	})
		// }
	},
	watch:{
		message(){
			this.$nextTick(()=>{
				let list = this.$refs.content
				list.scrollTop = list.scrollHeight
			})
		}
	}
}
</script>


<style scoped lang="stylus">
	.hello
		width : 100% 
		height: 100%
		.return
			font-size : 15px
			margin-top: -20px
		.robot-box
			width:330px
			height:550px
			position : absolute
			top: 0
			left:50%
			margin-top: 70px
			transform:translate(-50%,0)
			background-color : #fff
			// &::after
			// 	z-index:9 
			// 	position absolute
			// 	top:0px
			// 	left:0px
			// 	background-image : url("https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1551978983773&di=60fd982b279d786957b362faf168daff&imgtype=0&src=http%3A%2F%2Fpic32.nipic.com%2F20130816%2F13455593_183010394000_2.jpg")
			// 	background-size:100% 100%
			// 	width:500px
			// 	height:500px
			
			.title
				width:100%
				height: 50px
				background : #399aec
				color:#fff
				line-height : 50px
				text-align :center
				// margin-bottom : 30px
			.better-wrapper
				width:330px;
				height:550px
				.better-content
					overflow : auto
					width:100%
					height:450px
					margin-bottom:50px
					.bgImg
						background-image : url("https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1551978983773&di=60fd982b279d786957b362faf168daff&imgtype=0&src=http%3A%2F%2Fpic32.nipic.com%2F20130816%2F13455593_183010394000_2.jpg")
						background-size:100% 100%
						position absolute
						top:80px
						left:15px
						width:300px
						height:400px
						opacity 0.1
					.box
						padding : 10px 0
						.left
							max-width: 200px
							font-size 12px	
							display : flex
							.l-head
								width:40px
								height:40px
								background:url("https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1551978983773&di=60fd982b279d786957b362faf168daff&imgtype=0&src=http%3A%2F%2Fpic32.nipic.com%2F20130816%2F13455593_183010394000_2.jpg")	
								background-size:100% 100% 
								margin-left : 6px
								border:1px solid #ccc
								border-radius 6px
								display inline-block
							.l-cont
								display inline-block
								margin: 0px 0 0px 8px
								line-height: 18px
								padding:10px 9px 10px 9px
								box-sizing:border-box
								background : #90EE90
								border-radius:6px
								max-width: 150px
								position : relative
								&:after
									content: ""
									width : 0
									height : 0	
									border-width: 5px;
									border-style: solid;
									border-color: transparent #90EE90 transparent transparent;
									margin:0px 0 16px -93px
									position :absolute
									top : 10px
									left : 83px
						.right
							width：100%
							&:after
								content : ""
								display : block
								clear : both
							.r-cont
								max-width: 150px
								line-height: 18px
								padding:10px 9px 10px 9px
								box-sizing:border-box
								background : #90EE90
								border-radius:6px
								font-size 12px
								float: right
								margin : 0 8px 0 0
								position relative
								&:after
									content: ""
									width : 0
									height : 0
									border-width: 5px;
									border-style: solid;
									border-color: transparent transparent transparent #90EE90;
									margin:0px 0 16px -93px
									position :absolute
									top : 10px
									right : -10px
							.r-head
								margin : 0 6px 0 0
								float : right
								width : 40px
								height : 40px
								background : url("https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1551978258279&di=38793eee4a2ade560c11c94b746a2aef&imgtype=0&src=http%3A%2F%2Fp0.so.qhmsg.com%2Ft01fc45de21922ae6df.jpg")	
								background-size : 100% 100% 
							&:after
								content : ""
								display : block
								clear : both
			.bottom-box
				width: 100%
				height: 50px
				position : absolute
				bottom: 0
				left: 0px
				background : #eee
				display :flex
				justify-content: center
				align-items : center 
				.inp
					width: 200px
					height: 22px
					padding : 5px 8px
					outline : none 
					border: 1px solid #bbb
					border-radius: 5px
				.se
					margin-left:6px
					width:62px
					height:32px
					border-radius: 5px
					border:none
					background : #399aec
					color:#fff
					&:hover
						background : #55adf8






</style>













