import Vue from 'vue'
import Router from 'vue-router'
import firstPage from '@/components/firstPage'
import mainPage from "@/components/mainPage"
import register from "@/components/register"

Vue.use(Router)

export default new Router({
  // mode:"",
  routes: [
    {
      path: '/',
      name: 'firstPage',
      component: firstPage
    },
    {
      path: "/mainPage",
      name: "mainPage",
      component: mainPage
    },
    {
      path: "/register",
      name: "register",
      component: register
    }
  ]
})



