<template>
	<!-- <div class="first"> -->
		<div class="wrapper">
			<div class="head"></div>
			<h4 class="wel">欢迎光临</h4>
			<span class="wel-yw">Welcome to chat-Robot</span>
			<div class="login-box">
				<input class="user" v-model="us" type="text" placeholder="账号">
				<input class="pas" v-model="pas" type="password" placeholder="密码">
			</div>
			<div class="jzPas">
				<input class="pasK" type="checkbox" id="checkbox">
				<label for="checkbox">记住密码</label>
			</div>
			<input class="login" type="submit" @click= "login" value="登录">
			<router-link tag="span" class="name" to="/register">亲！注册账号点这里</router-link>
			<!-- <span class="name">吕星辰</span> -->
		</div>
	<!-- </div> -->
</template>

<script>
export default {
	name: 'firstPage',
	data () {
		return {
			us:"",
			pas: "" 
		}
	},
	methods:{
		login(){
			if(this.us.length && this.pas.length){
				this.$axios.get("http://localhost:12306/firstPage",{
					params:{
						us:this.us,
						pas:this.pas
				}}).then((res)=>{
					if(res.data == "ok"){
						console.log("ok")
						this.$router.push("/mainPage")
					}else if(res.data == "fail"){
						alert("账号密码不对")
						this.us = "";
						this.pas = "";
					}
				}).catch((e)=>{
					console.log(e)
				})
			}else{
				alert("请输入账号密码")
			}
		},
	},
}
</script>
<style scoped lang="stylus">

	// .first
	// 	width 100%
	// 	height 2000px
		.wrapper
			position absolute
			top 370px
			left 50%
			transform translate(-50%,-50%)
			display flex 
			flex-direction column
			align-items center
			box-sizing border-box
			padding 15px
			width 334px;
			height 380px;
			background #fff
			opacity 0.9
			border-radius:5px
			.head
				width 80px
				height 80px
				border-radius 50%
				background: url("https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1551978983773&di=60fd982b279d786957b362faf168daff&imgtype=0&src=http%3A%2F%2Fpic32.nipic.com%2F20130816%2F13455593_183010394000_2.jpg")
				background-size:100% 100%
				margin-top:-50px
			.wel
				color:#399aec
				margin-top:15px
			.wel-yw
				margin-top:-20px
				color:#399aec
				font-size:11px
			.login-box
				width:100%
				display flex
				flex-direction : column
				margin-top:18px
				.user
					height:26px
					padding:8px 12px
					font-size:15px
					border-radius:6px
					border:1px solid #bbb
					outline none
				.pas
					margin:15px 0
					height:26px
					padding:8px 12px
					font-size:15px
					border-radius:6px
					border:1px solid #bbb
					outline none
			.jzPas
				margin-right:-200px
				display flex
				justify-content center
				align-items center
				color:#666
				font-size:13px
				.pasK
					margin-right:8px
					width:16px
					height:16px
			.login
				width:100%
				height:40px
				font-size:16px
				margin-top:20px
				background #399aec
				border:none
				border-radius:6px
				color:#fff
			.name
				margin-top:16px
				color:#666
				font-size:13px
				cursor: pointer
				&:hover
					color:#399aec
</style>



